.contact-information {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100vw;
}

.contact {
    color: white;
    margin: auto;
    text-align: center;
    padding: 0 5em 0 5em;
    font-family: Garamond, "Times New Roman", Times, serif, serif;
}

@media screen and (max-width: 500px) {
    .logo-image {
        display: none;
    }
}

.contact h1 {
    text-decoration: underline;
}
.contact a {
    color: white;
}
.contact p {
    text-align: left;
}
.contact ul {
    list-style: none;
}
.logo {
    display: flex;
    flex-direction: row;
    justify-content: center;
    max-width: 500px;
    margin: auto;
    margin-bottom: 2em;
}

.notification .message {
    margin: auto;
    background-color: green;
    max-width: 60%;
    border-radius: 5px;
}
.notification .error {
    margin: auto;
    background-color: red;
    max-width: 60%;
    border-radius: 5px;
}
