nav {
    width: 80vw;
    background-color: black;
    display: flex;
    margin: auto;
    align-items: center;
    justify-content: center;
}

nav ul {
    margin: auto;
}

nav li {
    display: inline;
    padding: 0.125em;
}
nav li a {
    font-family: Garamond, Arial, sans-serif;
    color: white;
    text-decoration: none;
    font-size: 1.5em;

    background-color: black;
}
nav li a.current {
    color: black;
    background-color: white;
}

nav li a:hover {
    color: black;
    background-color: white;
}
